    }
    @media(max-width: $xs){
    
    }
    
  }
  &__map {
    width: calc(100% - 330px);
    border-radius: 20px;
    overflow: hidden;
    position: relative;
    min-height: 400px;
    img {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 15px;
      right: 15px;
      border-radius: 20px;
      width: calc(100% - 30px);
      height: 100%;
      object-fit: cover;

